class Popup {
  constructor(node, coverLayer) {
    this.node = node;
    this.name = node.querySelector('.person__name');
    this.rank = node.querySelector('.person__number');
    this.list = node.querySelector('.person__rank');
    this.description = node.querySelector('.popup__description');
    this.imgSmall = node.querySelector('.person__img-container img');
    this.imgBig = node.querySelector('.popup__img');
    this.btnClose = node.querySelector('.popup__close');
    this.status = false;
    this.smooth = null;

    this.coverLayer = coverLayer;

    this.btnClose.addEventListener('click', this.close.bind(this));
  }

  setSmooth(smooth) {
    this.smooth = smooth;
  }

  open() {
    this.coverLayer.style.pointerEvents = 'none';
    document.body.style.pointerEvents = 'none';
    this.status = true;
    this.node.classList.add('show');
    const mq = window.matchMedia('(min-width: 801px)');
    if (mq.matches) {
      this.smooth.cancelAnimationFrame();
    }
  }

  close(e) {
    e.preventDefault();
    this.node.classList.remove('show');
    this.coverLayer.style.pointerEvents = 'visible';
    document.body.style.pointerEvents = 'visible';
    this.status = false;
    const mq = window.matchMedia('(min-width: 801px)');
    if (mq.matches) {
      this.smooth.requestAnimationFrame();
    }
  }

  set(data) {
    this.name.innerHTML = data.name;
    this.rank.innerHTML = data.rank;
    this.list.innerHTML = data.list;
    this.description.innerHTML = data.description;
    this.imgSmall.src = data.imgSmall;
    this.imgBig.src = data.imgBig;
  }
}

export default Popup;