import 'nodelist-foreach-polyfill';

import Hammmer from 'hammerjs';
import Popup from './Popup';
import dataNl from './data-nl';
import dataFr from './data-fr';
import anime from 'animejs';
import Layer from './Layer';
import Smooth from './Smooth';
document.addEventListener('DOMContentLoaded', () => {
	// variables
	const body = document.querySelector('body');
	const coverLayer = document.querySelector('.cover-layer');
	const smoothLayer = document.querySelector('.cover-layer .smooth-nav');
	const coverSvgLayer = document.querySelector('.cover-svg-layer');
	const skew = document.querySelector('.skew');
	const topLayer = document.querySelector('.top-layer');
	const holeLayer = document.querySelector('.hole-layer');
	//const btnStart = document.querySelector('.top-layer button');
	const btnStart = document.querySelector('.eye__text');
	const cursor = document.querySelector('.cursor');
	let pos = { // position of cover
		x: 0,
		y: 0,
	}

	let center = {
		x: window.innerWidth / 2,
		y: window.innerHeight / 2 + 25,
	}
	let popup;
	let layerObjects = [];
	let smooth;
	let borders = smoothLayer.getBoundingClientRect();

	// functions
	const init = () => {
		// create popup
		popup = new Popup(document.querySelector('.popup'), smoothLayer);
		handleMarkers();
		btnStart.addEventListener('click', start);

		window.addEventListener('resize', setInitialButtonPos);
		setInitialButtonPos();

	}

	const setInitialButtonPos = () => {
		const mq = window.matchMedia('(min-width: 801px)');
		document.querySelectorAll('.eye').forEach((eye, i) => {
			eye.setAttribute('transform', `translate(${window.innerWidth / 2}, ${window.innerHeight / 2 + 50})`);
		});
	}

	const start = (e) => {
		topLayer.classList.add('started');
		holeLayer.classList.add('started');
		btnStart.classList.add('started');
		smooth = new Smooth(popup, layerObjects);
		popup.setSmooth(smooth);
		const mq = window.matchMedia('(min-width: 801px)');
		if (mq.matches) {
			cursor.style.transform = `translate3d(${e.clientX}px, ${e.clientY}px, 0)`;
			smooth.on();
			anime({
				targets: '#hole .eye',
				width: 350,
				height: 350,
				rx: 175,
				x: -175,
				y: -175,
				duration: 300,
				easing: 'easeInOutBack',
			})
		} else {
			smooth.onMobile();
			anime({
				targets: '#hole .eye',
				width: 300,
				height: 300,
				rx: 150,
				x: -150,
				y: -150,
				duration: 300,
				easing: 'easeInOutBack',
			})
		}
	}

	const handleMarkers = () => {
		const layers = coverSvgLayer.querySelectorAll('.layer');
		const lang = document.getElementsByTagName('html')[0].getAttribute('lang');

		if (lang == "nl") {
			layers.forEach(layer => {
				layerObjects.push(new Layer(layer, cursor, dataNl, popup));
			});
		}
		if (lang == "fr") {
			layers.forEach(layer => {
				layerObjects.push(new Layer(layer, cursor, dataFr, popup));
			});
		}

	}

	const initMobileControls = () => {
		// mobile controls
		let last_x = 0;
		let last_y = 0;
		const mc = new Hammmer.Manager(body);
		mc.add(new Hammmer.Pan({
			threshold: 0,
			pointers: 0
		}));
		mc.on('panmove panend', onPan);

		function onPan(e) {
			if (e.type === 'panmove') {
				// console.log(e.deltaX, e.deltaY);
				pos = {
					x: last_x + e.deltaX,
					y: last_y + e.deltaY,
				}
				const value = `translate3d(${pos.x}px, ${pos.y}px, 0px)`;
				smoothLayer.style.webkitTransform = value;
				smoothLayer.style.mozTransform = value;
				smoothLayer.style.transform = value;
			}

			layerObjects.forEach(layer => {
				layer.buttons.forEach(btn => {
					const pos = btn.getBoundingClientRect();
					const dist = Math.sqrt(Math.pow(Math.abs(center.x - pos.left), 2) + Math.pow(Math.abs(center.y - pos.top), 2));
					// const dist = Math.hypot(center.x - pos.left, center.y - center.top);
					if (dist < 120) {
						btn.classList.add('show');
					} else {
						btn.classList.remove('show');
					}
				});
			});

			if (e.type === 'panend') {
				last_x = pos.x;
				last_y = pos.y;
				console.log(last_x, last_y);
			}
		}
	}
	init();
});