class Layer {
  constructor(layer, cursor, data, popup) {
    this.layer = layer;
    this.cursor = cursor;
    this.data = data;
    this.popup = popup;
    this.id = layer.id;
    this.buttons = layer.querySelectorAll('.button');
    this.paths = layer.querySelectorAll('path');
    this.init();
  }

  init() {
    const mq = window.matchMedia('(min-width: 801px)');
    if (mq.matches) {
      this.paths.forEach(p => {
        p.addEventListener('mouseenter', (e) => {
          this.cursor.classList.add('show');
        })
        p.addEventListener('mouseleave', (e) => {
          this.cursor.classList.remove('show');
        });
        p.addEventListener('click', this.openPopup.bind(this));
      });
    } else {
      this.buttons.forEach(btn => {
        btn.addEventListener('click', this.openPopup.bind(this));
      });
    }
  }

  openPopup(e) {
    const data = this.data.filter(person => person.id === this.id);
    this.popup.set(data[0]);
    this.popup.open();
  }
}

export default Layer;