import Hammmer from 'hammerjs';
class Smooth {
  constructor(popup, layerObjects) {
    this.bindMethods()

    this.data = {
      ease: 0.1,
      current: {
        x: 0,
        y: 0,
      },
      last: {
        x: 0,
        y: 0
      },
      delta: {
        x: 0,
        y: 0,
      },
      lastPan: {
        x: 0,
        y: 0,
      }
    }
    this.popup = popup;
    this.layerObjects = layerObjects;

    // mouse
    this.mouse = {
      x: window.innerWidth / 2,
      y: window.innerHeight / 2,
    }

    this.center = {
      x: window.innerWidth / 2,
      y: window.innerHeight / 2 + 25,
    }
    this.currentEvent = null;
    this.prevEvent = null;

    this.config = {
      height: window.innerHeight,
      width: window.innerWidth,
      padding: 150,
      offset: 50,
      speed: 10,
    }

    this.math = {
      lerp: (a, b, n) => {
        return (1 - n) * a + n * b
      },
      norm: (value, min, max) => {
        return (value - min) / (max - min)
      }
    }

    this.dom = {
      cover: document.querySelector('.cover-layer .smooth-nav'),
      skew: document.querySelector('.skew'),
      cursor: document.querySelector('.cursor'),
      eyes: document.querySelectorAll('.eye'),
    }

    this.rAF = null

    this.init();
  }

  bindMethods() {
    ['run', 'requestAnimationFrame', 'cancelAnimationFrame', 'onPan']
      .forEach((fn) => this[fn] = this[fn].bind(this))
  }

  handleMouseMove(event) {
    this.currentEvent = event;
    if (event && !this.popup.status) {
      this.mouse = {
        x: event.clientX,
        y: event.clientY,
      }
      this.dom.cursor.style.transform = `translate3d(${this.mouse.x}px, ${this.mouse.y}px, 0)`;

      this.dom.eyes.forEach((eye, i) => {
        eye.setAttribute('transform', `translate(${this.mouse.x}, ${this.mouse.y}) scale(${1 - (i * 0.08)})`);
      });

      if (this.currentEvent && this.prevEvent) {
        this.data.current.x -= this.currentEvent.screenX - this.prevEvent.screenX;
        this.data.current.y -= this.currentEvent.screenY - this.prevEvent.screenY;
      }
    }
  }

  run() {
    // if (this.currentEvent && this.prevEvent) {
    //   this.data.delta.x = this.currentEvent.screenX - this.prevEvent.screenX;
    //   this.data.delta.y = this.currentEvent.screenY - this.prevEvent.screenY;
    // }
    this.prevEvent = this.currentEvent;

    const diffX = this.data.delta.x;
    const accX = diffX / this.config.width;
    const veloX = +accX;
    const skewX = veloX * 117.5;

    const diffY = this.data.delta.y;
    const accY = diffY / this.config.height;
    const veloY = +accY;
    const skewY = veloY * 1000;

    // console.log(skewX, skewY);
    // this.dom.skew.style.transform = `skew(${skewX}deg, ${-skewY}deg)`;

    // move parent
    this.moveParent();

    this.requestAnimationFrame();
  }

  onPan(e) {
    if (e.type === 'panmove') {
      this.data.current.x = this.data.lastPan.x + e.deltaX;
      this.data.current.y = this.data.lastPan.y + e.deltaY;
    }

    this.layerObjects.forEach(layer => {
      layer.buttons.forEach(btn => {
        const pos = btn.getBoundingClientRect();
        const dist = Math.sqrt(Math.pow(Math.abs(this.center.x - pos.left), 2) + Math.pow(Math.abs(this.center.y - pos.top), 2));
        // const dist = Math.hypot(center.x - pos.left, center.y - center.top);
        if (dist < 120) {
          btn.classList.add('show');
        } else {
          btn.classList.remove('show');
        }
      });
    });

    if (e.type === 'panend') {
      this.data.lastPan.x = this.data.current.x;
      this.data.lastPan.y = this.data.current.y;
    }
  }

  moveParent() {
    const mq = window.matchMedia('(min-width: 801px)');
    if (mq.matches) {
      this.data.border = this.dom.cover.getBoundingClientRect();
      const { border } = this.data;
      const { padding, offset, speed } = this.config;
      if (this.mouse.x < padding && border.left < -offset) { // left
        this.data.last.x += speed;
        this.data.current.x = this.data.last.x;
        this.dom.cover.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0)`;
      } else if (this.mouse.x > window.innerWidth - padding && border.right > window.innerWidth + offset) { // right
        this.data.last.x -= speed;
        this.data.current.x = this.data.last.x;
        this.dom.cover.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0)`;
      } else if (this.mouse.y < padding && border.top < -offset) { // top
        this.data.last.y += speed;
        this.data.current.y = this.data.last.y;
        this.dom.cover.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0)`;
      } else if (this.mouse.y > window.innerHeight - padding && border.bottom > window.innerHeight + offset) { // bottom
        this.data.last.y -= speed;
        this.data.current.y = this.data.last.y;
        this.dom.cover.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0)`;
      } else {
        // this.data.last.x = this.math.lerp(this.data.last.x, this.data.current.x, this.data.ease);
        // this.data.last.y = this.math.lerp(this.data.last.y, this.data.current.y, this.data.ease);
        // this.dom.cover.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0)`;
      }
    }
    this.data.last.x = this.math.lerp(this.data.last.x, this.data.current.x, this.data.ease);
    this.data.last.y = this.math.lerp(this.data.last.y, this.data.current.y, this.data.ease);
    this.dom.cover.style.transform = `translate3d(${this.data.last.x}px, ${this.data.last.y}px, 0)`;
  }

  onMobile() {
    this.addMobileEvents();
    requestAnimationFrame(this.run);
  }

  on() {
    this.addEvents();
    requestAnimationFrame(this.run);
  }

  off() {
    this.cancelAnimationFrame();
  }

  requestAnimationFrame() {
    this.rAF = requestAnimationFrame(this.run);
  }

  cancelAnimationFrame() {
    cancelAnimationFrame(this.rAF);
  }

  destroy() {
    this.data = null;
    this.cancelAnimationFrame();
  }

  addMobileEvents() {
    this.mc = new Hammmer.Manager(document.body);
    this.mc.add(new Hammmer.Pan({
      threshold: 0,
      pointers: 0
    }));
    this.mc.on('panmove panend', this.onPan);
  }

  addEvents() {
    document.onmousemove = this.throttle((e) => this.handleMouseMove(e), 25);
    document.body.addEventListener('mouseenter', this.requestAnimationFrame);
    document.body.addEventListener('mouseleave', this.cancelAnimationFrame);
  }

  throttle(fn, ms) {
    var time, last = 0;
    return function () {
      var a = arguments,
        t = this,
        now = +(new Date),
        exe = function () {
          last = now;
          fn.apply(t, a);
        };
      clearTimeout(time);
      (now >= last + ms) ? exe() : time = setTimeout(exe, ms);
    }
  }

  removeEvents() {
    this.dom.coverLayer.removeEventListener('mouseenter', this.requestAnimationFrame, { passive: true })
    this.dom.coverLayer.removeEventListener('mouseleave', this.cancelAnimationFrame, { passive: true })
  }

  init() {
    // this.on();
  }
}
export default Smooth;